.form_container {
    align-items: center;
    margin: 2em;
    padding-top: 154px;
    padding-bottom: 94px;
    padding-left: 50px;
    padding-right: 50px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

.label {
    margin: 20px 0 10px;
}

.input {
    box-shadow: inset 0 0 15px 0 #c7c7c7;
}

.input:focus {
    box-shadow: 3px 4px 5px 6px #c7c7c7;
}
