.cta {
    width: 100%;
    background: #715691;
    margin-top: 100px;
}

.cta_content {
    width: 100%;
    height: 135px;
    padding-left: 5px;
}

.cta_title {
    display: inline-block;
    font-size: 36px;
    font-weight: 700;
    color: #FFFFFF;
    line-height: 1.2;
}

.cta_price {
    font-size: 48px;
    color: #1befc5;
    font-weight: 700;
    -webkit-transform: translateY(-8px);
    -moz-transform: translateY(-8px);
    -ms-transform: translateY(-8px);
    -o-transform: translateY(-8px);
    transform: translateY(-8px);
}

.cta_price span {
    font-size: 16px;
}

.cta_price span:first-child {
    margin-right: 5px;
}

.cta_button {
    width: 148px;
    height: 63px;
    border-radius: 12px;
    background: #1befc5;
    text-align: center;
    -webkit-transform: translateY(4px);
    -moz-transform: translateY(4px);
    -ms-transform: translateY(4px);
    -o-transform: translateY(4px);
    transform: translateY(4px);
    -webkit-transition: all 200ms ease;
    -moz-transition: all 200ms ease;
    -ms-transition: all 200ms ease;
    -o-transition: all 200ms ease;
    transition: all 200ms ease;
}

.cta_button a {
    display: block;
    font-size: 16px;
    font-weight: 700;
    color: #35214d;
    line-height: 63px;
}

.cta_button:hover {
    background: #26064c;
}

.cta_button:hover a {
    color: #FFFFFF;
}

@media only screen and (max-width: 991px) {
    .cta {
        height: fit-content;
        padding: 20px;
    }

    .cta_title {
        font-size: 24px;
        padding-top: 10px;
    }

    .cta_price {
        font-size: 20px;
        padding-top: 10px;
    }

    .cta_button {
        width: 120px;
        height: 40px !important;
    }

    .cta_button a {
        font-size: 13px;
        width: 120px;
        height: fit-content;
    }
}