
.contact {
    width: 100%;
    background: #FFFFFF;
    padding-top: 95px;
    padding-bottom: 96px;
}

.form_container {
    margin-top: 100px;
}

.contact_title {
    font-size: 30px;
    font-weight: 700;
    color: #1f1d22;
    line-height: 0.75;
}

.contact_text {
    margin-top: 33px;
}

.contact_text p {
    text-align: justify;
    line-height: 1.9;
    color: #656564;
}

.contact_logo {
    margin-top: 29px;
}

.contact_logo a {
    font-size: 36px;
    font-weight: 600;
    color: #7836c6;
}

.contact_logo a span {
    color: #1befc5;
}

.contact_list {
    margin-top: 22px;
    max-width: 160px;
}

.contact_list ul li {
    font-size: 15px;
    font-weight: 500;
    color: #1f1d22;
    line-height: 1.6;
}

.contact_list ul li:not(:last-of-type) {
    margin-bottom: 25px;
}

.contact_social {
    margin-top: 43px;
}

.contact_social ul li:not(:last-child) {
    margin-right: 22px;
}

.contact_social ul li a i {
    padding: 5px;
    color: #474747;
}

.contact_social ul li a i:hover {
    color: #1befc5;
}

.message_form {
    margin-top: 40px;
}

.message_input {
    width: 100%;
    height: 54px;
    background: #f1f6f8;
    border: none;
    outline: none;
    padding-left: 34px;
}

.message_input:not(:last-child) {
    margin-bottom: 8px;
}

.message_input::-webkit-input-placeholder {
    font-size: 12px !important;
    font-weight: 500 !important;
    font-style: italic;
    color: #aeaeae !important;
}

.message_input:-moz-placeholder {
    font-size: 12px !important;
    font-weight: 500 !important;
    font-style: italic;
    color: #aeaeae !important;
}

.message_input::-moz-placeholder {
    font-size: 12px !important;
    font-weight: 500 !important;
    font-style: italic;
    color: #aeaeae !important;
}

.message_input:-ms-input-placeholder {
    font-size: 12px !important;
    font-weight: 500 !important;
    font-style: italic;
    color: #aeaeae !important;
}

.message_input::input-placeholder {
    font-size: 12px !important;
    font-weight: 500 !important;
    font-style: italic;
    color: #aeaeae !important;
}

.message_textarea {
    height: 250px;
    padding-top: 17px;
}

.message_form_button {
    width: 130px;
    height: 60px;
    background: #1befc5;
    border: none;
    outline: none;
    font-size: 20px;
    font-weight: 600;
    color: #1f1d22;
    border-radius: 12px;
    cursor: pointer;
    margin-top: 3px;
}

@media only screen and (max-width: 768px) {
    .message_form_container {
        margin-top: 50px;
    }
}
