.hero_background {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 820px;
    background-color: #4d1d85;
    background-image: linear-gradient(to bottom, #200e35, #4d1d85);
    background: #4d1d85;
    background: linear-gradient(to bottom, #200e35, #4d1d85);
}

.background_city {
    z-index: 3;
}

.background_image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 820px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
}

.hero_container {
    position: absolute;
    left: 0;
    top: 31.9%;
    width: 100%;
    z-index: 5;
    height: 700px;
}

.hero_text_container {
    height: 500px !important;
}

.hero_title {
    font-size: 60px;
    font-weight: 600;
    color: #FFFFFF;
    line-height: 1.2;
}

.hero_subtitle {
    max-width: 750px;
    font-size: 14px;
    color: #FFFFFF;
    line-height: 2.14;
    margin-left: auto;
    margin-right: auto;
    margin-top: 9px;
}

.get_started_btn {
    width: 166px;
    height: 60px;
    background: #1befc5;
    border-radius: 12px;
    border: none;
    outline: none;
    cursor: pointer;
    font-size: 24px;
    font-weight: 600;
    color: #1f1d22;
}

@media only screen and (max-width: 768px) {
    .hero_title {
        font-size: 30px;
    }

    .get_started_btn {
        width: 120px;
        height: 40px;
        font-size: 18px;
        font-weight: 600;
        color: #1f1d22;
    }
}

@media only screen and (min-width: 769px) and (max-width: 1024px) {
    .hero_title {
        font-size: 45px;
    }

    .get_started_btn {
        width: 135px;
        height: 50px;
        font-size: 22px;
        font-weight: 600;
        color: #1f1d22;
    }
}

