.form {
    z-index: 999;
    position: relative;
}


.login-body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100vh
}

.loginForm {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 480px;
    padding: 90px 45px 60px 45px;
    transform: translate(-50%, -50%);
    background: rgba(0, 0, 0, 0.4);
    box-shadow: 0 15px 20px rgba(0, 0, 0, 0.2);
    border-radius: 5px
}

@media (max-width: 575px) {
    .loginForm {
        width: 282px;
        padding: 90px 15px 15px 15px
    }
}

.loginForm .logo-login img {
    width: 99px;
    height: 95px;
    position: absolute;
    top: -57px;
    left: 0;
    right: 0;
    margin: 0 auto;
    border: 15px solid rgba(255, 255, 255, 0.1);
    box-shadow: 0 15px 20px rgba(0, 0, 0, 0.7);
    border-radius: 50%
}

.loginForm h2 {
    color: #fff;
    font-size: 30px;
    margin: 0;
    text-align: center;
    font-weight: 600;
    margin-bottom: 40px
}

.loginForm .formInput {
    position: relative;
    margin: 20px 0
}

.loginForm .formInput label {
    font-size: 18px;
    font-weight: 600;
    color: #fff
}

.loginForm .formInput input {
    outline: none;
    height: 40px;
    padding: 10px;
    box-sizing: border-box;
    width: 100%;
    border: 1px solid rgba(0, 0, 0, 0.5);
    background: transparent;
    font-size: 18px;
    border: none;
    color: #fff;
    border-bottom: 1px solid #fff
}

.loginForm .formInput input::placeholder {
    color: #F067FF
}

.loginForm .formInput input[type="submit"] {
    background: #F067FF;
    color: #fff;
    border: none;
    cursor: pointer;
    text-transform: capitalize;
    line-height: 1;
    font-size: 18px;
    font-weight: 700;
    border-radius: 20px;
    transition: .4s
}

.loginForm .formInput input[type="submit"]:hover {
    background: #000;
    background: #F067FF
}

.loginForm .forget {
    display: block;
    margin-top: 30px;
    color: #F067FF;
    font-weight: 600;
    text-decoration: underline;
    margin-bottom: 5px
}

.loginForm .forget:hover {
    color: #fff
}

.loginForm .registration {
    display: block;
    color: #F067FF;
    font-weight: 600;
    text-decoration: underline;
    margin-bottom: 5px
}

.loginForm .registration:hover {
    color: #fff
}