.header {
    padding: 6px 0 0;
    background-color: #200e35;
    position: fixed;
    z-index: 1030;
    width: 100%;
}

.main_nav {
    padding: 0 16px;
    height: 80px;
}

.logo {
    width: 100px;
}

.first_ul li:not(:last-child) {
    margin-right: 48px;
}

.first_ul li a {
    font-size: 16px !important;
    font-weight: 600 !important;
    font-family: 'Raleway', sans-serif;
    color: #ffffff !important;
}

.second_ul li:not(:last-child) {
    margin-right: 18px;
}

.second_ul li a {
    font-size: 16px !important;
    font-weight: 600 !important;
    font-family: 'Raleway', sans-serif;
    color: #1befc5 !important;
}

.first_ul li:hover {
    border: 1px solid salmon;
    box-shadow: 10px 0 0 salmon;
}
