.footer {
    width: 100%;
    background: #26064c;
    padding-top: 95px;
    padding-bottom: 95px;
}

.footer_logo {
    font-size: 47px;
    color: #FFFFFF;
    font-weight: 600;
    line-height: 0.75;
}

.footer_logo span {
    color: #1befc5;
}

.copyright {
    font-size: 11px;
    font-weight: 400;
    color: #ffffff;
    margin-top: 17px;
}

.copyright p {
    color: #FFFFFF;
}

.footer_text {
    margin-top: 33px;
}

.footer_text p {
    color: #FFFFFF;
}

.contact_container {
    margin-top: 38px;
}

.contact_form>div:not(:last-of-type) {
    margin-bottom: 21px;
}

.contact_input {
    width: 100%;
    height: 49px;
    border: none;
    outline: none;
    border-radius: 12px;
    background: #715691;
    padding-left: 29px;
    color: #FFFFFF;
}

.contact_textarea {
    height: 142px;
    padding-top: 15px;
}

.contact_input::-webkit-input-placeholder {
    font-size: 12px !important;
    font-weight: 400 !important;
    color: #FFFFFF !important;
}

.contact_input:-moz-placeholder {
    font-size: 12px !important;
    font-weight: 400 !important;
    color: #FFFFFF !important;
}

.contact_input::-moz-placeholder {
    font-size: 12px !important;
    font-weight: 400 !important;
    color: #FFFFFF !important;
}

.contact_input:-ms-input-placeholder {
    font-size: 12px !important;
    font-weight: 400 !important;
    color: #FFFFFF !important;
}

.contact_input::input-placeholder {
    font-size: 12px !important;
    font-weight: 400 !important;
    color: #FFFFFF !important;
}

.contact_button {
    width: 108px;
    height: 52px;
    background: #1befc5;
    border: none;
    outline: none;
    border-radius: 12px;
    cursor: pointer;
    font-size: 16px;
    font-weight: 700;
    color: #35214d;
    margin-top: 23px;
}

.footer_links {
    padding-top: 13px;
}

.footer_list_col:not(:nth-child(n+3)) {
    margin-bottom: 45px;
}

.footer_list_title {
    font-size: 20px;
    font-weight: 700;
    color: #1befc5;
}

.footer_list {
    margin-top: 36px;
    padding-left: 0;
}

.footer_list li:not(:last-of-type) {
    margin-bottom: 14px;
}

.footer_list li a {
    font-size: 15px;
    font-weight: 400;
    color: #FFFFFF;
    text-decoration: none;
}

.footer_list li a:hover {
    color: #1befc5;
}