.why {
    width: 100%;
    background: #FFFFFF;
    padding-top: 94px;
    padding-bottom: 94px;
}

.whyContainer {
    margin-top: 100px;
}

.why_text {
    margin-top: 51px;
}

.why_text p {
    font-family: 'Raleway', sans-serif;
    font-size: 15px;
    line-height: 1.9;
    font-weight: 400;
    color: #656566;
    -webkit-font-smoothing: antialiased;
    text-shadow: rgb(0 0 0 / 1%) 0 0 1px;
}

.why_list {
    margin-top: 38px;
}

.why_list ul li {
    font-size: 15px;
    font-weight: 400;
    color: #656566;
}

.why_list ul li:not(:last-of-type) {
    margin-bottom: 17px;
}

.why_list ul li>div {
    width: 21px;
    height: 21px;
    border-radius: 50%;
    background: #1befc5;
    margin-right: 12px;
}

.why_image {
    text-align: center;
    right: 15px;
    width: 540px;
    height: 330px;
}

.why_image img {
    max-width: 100%;
    margin-top: 90px;
}

.section_title h2 {
    color: #1f1d22;
    line-height: 0.75;
    font-weight: 600;
    font-family: 'Raleway', sans-serif;
}
