.services_container {
    background-image: url('../../../images/services.jpg');
    width: 100%;
    height: fit-content;
}

.services {
    width: 100%;
    height: fit-content;
    padding-top: 96px;
    padding-bottom: 127px;
}

.service {
    width: 100%;
    height: 480px;
    background: #FFFFFF;
    border-radius: 12px;
    padding-top: 50px;
    padding-bottom: 66px;
    padding-left: 30px;
    padding-right: 30px;
    border: solid 2px transparent;
}

.services_row {
    margin-top: 15px;
}

.service_icon {
    width: 75px;
    height: 75px;
}

.service_icon img {
    width: 75px;
}

.serviceHeader h2 {
    color: #FFFFFF;
    font-weight: 700;
}

.service_title {
    margin-top: 39px;
}

.service_title h3 {
    font-family: 'Raleway', sans-serif;
    font-size: 24px !important;
    font-weight: 700;
}

.service_text {
    margin-top: 5px;
}

.service_text p {
    height: 145px;
    font-family: 'Raleway', sans-serif;
    font-size: 15px !important;
    line-height: 1.9;
    font-weight: 400;
    color: #656566;
    -webkit-font-smoothing: antialiased;
    text-shadow: rgb(0 0 0 / 1%) 0 0 1px;
}

.service_button {
    bottom: -32px;
    left: 50%;
    width: 157px;
    height: 63px;
    background: #f4f7f8;
    border-radius: 12px;
}

.service_button:hover {
    background: #1befc5;
}

.service_button a {
    display: block;
    font-size: 16px;
    font-weight: 600;
    color: #656566;
    line-height: 63px;
    text-decoration: none;
}

.service_button:hover a {
    color: #1f1d22;
}

@media only screen and (max-width: 425px) {
    .service {
        margin-top: 15px;
    }

    .service_title h3 {
        font-size: 20px;
    }

    .service_text p {
        font-size: 13px !important;
        margin-bottom: 0;
    }
}

@media only screen and (min-width: 426px) and (max-width: 768px) {
    .service {
        margin-top: 10px;
    }

    .service_title h3 {
        font-size: 20px;
    }

    .service_text p {
        font-size: 13px !important;
        margin-bottom: 0;
    }
}

