.package_starter {
    margin-top: 100px;
}

.pricing {
    width: 100%;
    background: #FFFFFF;
    padding-top: 94px;
    padding-bottom: 98px;
    color: #000;
}

.pricing_container {
    width: 100%;
    border-radius: 12px;
    border: solid 2px #d5dfe3;
    overflow: hidden;
}

.pricing_item {
    width: 100%;
    border: 2px solid #c2c2c2;
    border-top-right-radius: 10px !important;
}

.pricing_item:not(:last-child) {
    border-right: solid 2px #d5dfe3;
}

.pricing_title {
    height: 129px;
    background: #4d1d85;
    font-size: 30px;
    font-weight: 700;
    color: #FFFFFF;
    line-height: 129px;
    -webkit-transition: all 200ms ease;
    -moz-transition: all 200ms ease;
    -ms-transition: all 200ms ease;
    -o-transition: all 200ms ease;
    transition: all 200ms ease;
    border-top-right-radius: 10px !important;
}

.pricing_item:hover .pricing_title {
    color: #1befc5;
}

.pricing_content {
    padding-top: 50px;
    padding-bottom: 37px;
}

.package_price {
    font-size: 32px;
    font-weight: 700;
    color: #715691;
}

.package_price span {
    display: inline-block;
    position: relative;
    font-size: 11px;
    font-weight: 500;
    color: #1f1d22;
    -webkit-transform: translate(-5px, 12px);
    -moz-transform: translate(-5px, 12px);
    -ms-transform: translate(-5px, 12px);
    -o-transform: translate(-5px, 12px);
    transform: translate(-5px, 12px);
}

.pricing_list {
    margin-top: 50px;
}

.pricing_list ul {
    padding-left: 0;
}

.pricing_list ul li:not(:last-child) {
    margin-bottom: 26px;
}

.pricing_list ul li span {
    font-size: 16px;
    font-weight: 500;
    color: #656566;
    margin-left: 10px;
}

.pack_ok span {
    color: #1f1d22 !important;
    font-weight: 600 !important;
}

.pricing_button {
    width: 132px;
    height: 53px;
    margin-top: 75px;
    background: #1befc5;
    border-radius: 12px;
    -webkit-transition: all 200ms ease;
    -moz-transition: all 200ms ease;
    -ms-transition: all 200ms ease;
    -o-transition: all 200ms ease;
    transition: all 200ms ease;
}

.pricing_button a {
    display: block;
    font-size: 16px;
    font-weight: 700;
    color: #35214d;
    line-height: 53px;
    text-decoration: none;
}

.pricing_button:hover {
    background: #7836c6;
}

.pricing_button:hover a {
    color: #FFFFFF;
}

@media only screen and (max-width: 768px) {
    .pricing_item {
        margin-top: 20px;
    }
}
