@import url('https://fonts.googleapis.com/css2?family=Gloock&display=swap');

.intro {
    width: 100%;
    background: #FFFFFF;
    padding-top: 96px;
    padding-bottom: 97px;
}

.intro_row {
    margin-top: 98px;
}

.section_title h2 {
    left: 0;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    font-family: 'Gloock', serif;
    font-size: 30px;
    color: #1f1d22;
}

.intro_icon_container {
    width: 143px;
    height: 143px;
    border-radius: 50%;
    background: #d5dfe3;
}

.intro_col:nth-child(3) .intro_icon_container {
    background: #1befc5;
}

.intro_icon_container::before {
    position: absolute;
    width: 188px;
    height: 188px;
    border-radius: 50%;
    z-index: 0;
    background: rgba(213, 223, 227, 0.25);
    content: '';
}

.intro_col:nth-child(3) .intro_icon_container::before {
    background: rgba(27, 239, 197, 0.25);
}

.intro_icon {
    width: 56px;
    height: 56px;
}

.intro_icon img {
    max-width: 100%;
}

.intro_item_content {
    padding-top: 66px;
}

.intro_item_title {
    font-family: 'Raleway', sans-serif;
    font-size: 24px;
    font-weight: 700 !important;
    color: #1f1d22;
    line-height: 1.2;
}

.intro_item_text {
    margin-top: 27px;
}

.intro_item_text p {
    text-align: justify;
    line-height: 1.9;
    color: #656566;
}

.intro_button {
    width: 257px;
    height: 63px;
    border-radius: 12px;
    background: #715691;
    margin-top: 55px;
    border: 0;
    border-color: transparent;
    color: #FFFFFF;
    font-weight: 600;
}

.intro_button:hover {
    background: #1befc5;
    color: #1f1d22;
    font-weight: 600;
}

.intro_button button {
    display: block;
    font-size: 16px;
    font-weight: 700;
    color: #FFFFFF;
    line-height: 63px;
}





