
.team {
    width: 100%;
    background: #FFFFFF;
    padding-top: 95px;
    padding-bottom: 95px;
}

.section_text {
    max-width: 990px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
    text-align: justify;
    line-height: 1.9;
    color: #656566;
    font-size: 15px;
}

.team_row {
    margin-top: 86px;
}

.team_image {
    width: 100%;
}

.team_image img {
    max-width: 100%;
}

.team_content {
    left: 30px;
    bottom: 25px;
    z-index: -1;
    margin: -55px 0 0 10px;
}

.team_name a {
    font-size: 20px;
    font-weight: 500;
    color: #FFFFFF;
    line-height: 1.2;
}

.team_title {
    font-size: 14px;
    font-weight: 400;
    color: #FFFFFF;
    line-height: 1.2;
    margin-top: 6px;
    padding-left: 2px;
}
